@import "variables.scss";

.header-navigation{
 &__wrapper {
   width: 24px;
   display: flex;
 }

 &__wrapper:focus-within &__list {
  visibility: visible;
  left: 0px;
}

 &__toggle {
   display: flex;
   align-items: center;
   position: relative;
   top: 50%;
   transform: translateY(-50%);
   right: 0;
   width: 26px;
   height: 26px;
   cursor: pointer;
  }

 &__toggle > span,
 &__toggle > span::before,
 &__toggle > span::after {
   display: block;
   position: absolute;
   width: 100%;
   height: 2px;
   background-color: #000000;
 }

 &__toggle > span::before {
   content: "";
   top: -8px;
 }

 &__toggle > span::after {
   content: "";
   top: 8px;
 }

 &__list {
   display: block;
   position: absolute;
   visibility: hidden;
   top: 55px;
   width: 320px;
   height: 100%;
   padding: 0;
   list-style: none;
   z-index: 1;
 }

 &__link {
   display: block;
   padding: 22px 24px;
   font-family: "Oswald", sans-serif;
   font-size: 20px;
   font-weight: 600;
   border-top: $border-navigation__link;
 }

 &__item {
   line-height: 20px;
   text-transform: uppercase;
   text-align: center;
   background-color: $background-body;
 }

  &__toggle:focus-within > span {
    transform: rotate(45deg);
  }

  &__toggle:focus-within > span::before {
    display: none;
  }

  &__toggle:focus-within > span::after {
    top: 0;
    transform: rotate(90deg);
    visibility: visible;
  }
}
